body {
    background-color: floralwhite;
    margin: 0;
    padding: 0;
    font-family: "Helvetica Neue";
    font-weight: bold;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.top-bar {
    width: 100%;
    text-align: center;
}

/* NAVIGATION */
.nav {
    width: 100%;
    text-align: center;
}
  /* TOP BAR */
.nav .top-bar button{
    font-size: 14pt;
    color: darkcyan;
}

/* RECIPE LIST */
.recipe-list{
    padding: 1em;
    text-align: center;

}
.recipe-list h2 {
    font-size: 20pt;
    font-weight: bold;
}

/* RECIPE BLOCK */
.recipe-header {
    max-width: 30%;
    background-color: antiquewhite;
    margin: 5px;
    padding: 1em;
    border: thin dotted;
    display: inline-block;
}
.recipe-header h2 {
    color: darkcyan;
    font-size: 20pt;
}
.recipe-header h4 {
    color: lightseagreen;
    font-size: 12pt;
}
.recipe-header p {
    font-size: 11pt;
    font-style: italic;
}
.recipe-header img {
    max-width: 20%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 25px;
    padding: 1em;
}

/* RECIPE-INFO */

.recipe-info h2 {
    margin-left: 2em;
}

.recipe-info img {
    max-width: 15%;
    margin-left: 2em;
    border-style: double;
}

.author {
    margin-left: 2em;
}

.description {
    margin-left: 2em;
}

.ingredients {
    margin-left: 2em;
}

.instructions {
    margin-left: 2em;
}

/* RELATED-RECIPES */

.related-recipes h4{
    margin-left: 1em;
}
section input {
    display: block;
}

.add-a-recipe {
    display: inline-block;
    text-align: left;
    width: 54rem;
    box-shadow: rgba(0, 0, 0, 0.3) 0 0 1rem;
    padding: 2rem;
    box-sizing: border-box;
}

.app {
    text-align: center;
}
.recipe {
    text-align: left;
}

.recipe img {
    max-width: 100%;
    width: 100%;
    margin: 0;
    margin-top: -10vw;
    border: none;
}

.recipe .img-container {
    display: inline-block;
    width: 50%;
    height: 30rem;
    overflow: hidden;
}

.recipe-title-area {
    display: inline-block;
    width: 50%;
    vertical-align: top;
}

